.container {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-weight: bold;
}

.container > * {
    margin: 12px;
}

.inputColor {
    border: none;
    width: 30px;
    height: 30px;
    background-color: transparent;
    outline: none;
    margin: 3px;
}


.button1 {
    padding: 12px 30px;
    background-color: #ffd83d;
    border: none;
    border-radius: 6px;
    font-size: 16px;
    box-shadow: 0 4px 12px rgba(80,64,0,0.2);
    cursor: pointer;
}